import React, { useState, useEffect } from "react";
import { Card } from "react-bootstrap";
import { Carousel } from "react-responsive-carousel"
import { FaRegStar } from "react-icons/fa"
import 'react-responsive-carousel/lib/styles/carousel.min.css'

function PortCard(props) {
  // ({"title":"title",
  // "description":["p","p"],
  // "technologies":["t","t"],
  // "images":["ref","ref"]
  // "deployed":"url",
  // "repo":"url"})
  const [carousel, setCarousel] = useState([{ "url": "", "alt": "" }])
  const project = props.project

  useEffect(() => {
    setCarousel(project.images)
  }, [project.images])


  return (
    <Card className="portCard mx-md-auto my-2">
      <Card.Body>
        <h3 className="card-title">{project.title}</h3>
        <Carousel dynamicHeight={false} showThumbs={false} className="mb-2 card-img shadowed" style={{ backgroundColor: "#ffffff" }}>
          {carousel.map((img, i) => {
            return <div key={i}>
              <img className="image-fluid" src={img.url} alt={img.alt} />
            </div>
          })}
        </Carousel>
        {project.description.map((text, i) => {
          return <Card.Text key={i}>{text}</Card.Text>
        })}
        {project.technologies[0] && (<>
          <Card.Text className="font-weight-bold mb-0">Key technologies:</Card.Text>
          <Card.Text>{project.technologies.map((tech, i) => {
            return <span key={i} className="mr-2 d-inline-block"><FaRegStar /> {tech} </span>
          })}</Card.Text>
        </>)}
        <hr className="mt-0" />
        <div className="linkBox mx-auto">
          <a target="_blank" href={project.deployed} rel="noopener noreferrer" className="btn btn-outline-info mx-1">Live App</a>
          <a target="_blank" href={project.repo} rel="noopener noreferrer" className="btn btn-outline-info mx-1">GitHub Repo</a>
        </div>
      </Card.Body>
    </Card>
  );
}


export default PortCard;
